import request from '../../jsonp-req.js'
import { resolve } from 'path'
export default function(config,version){
    return {
         message_send(payload){
            let params = Object.entries(payload)
            params.forEach((tupl,i) =>{
               params[i] =  tupl.join('=')
            })
            params = params.join('&')
            return new Promise((resolve,reject) =>{
                request.get('https://api.vk.com/method/messages.send?group_id=' + config.group_id +   '&' + params +  '&access_token=' + config.token + '&v=' + version)
                .then(res =>{
                    resolve(res)
                })
            })
            
        }
    }
    
}


