<template>
    <div class = "node container" v-bind:ref = "'node'" v-bind:class = "{ draggable : draggable, yellow : isStart}" 
        v-bind:style = "{ left : geometry.posX + 'px' , top : geometry.posY + 'px'  }" 
        v-on:mousedown.exact = " startDrag($event) " 
        v-on:click.ctrl = "$emit('selectnode', { index : id })"
        v-on:click.alt.stop = "$emit('deletenode', { index : id })"
        >
        <div class = "entry-marker" v-on:click = "$emit('markasstart')"><i class="material-icons" >done</i></div>
        <div class = "delete-button" v-on:click = "$emit('deletenode')"><i class="material-icons" >close</i></div>
        <div class ="row">
            <h3><textarea class = "node_text col s12 materialize-textarea" v-model = "message" v-on:mousedown.stop = ""></textarea></h3>
            <ul class = "node_keyboard col s12 collection" v-bind:ref = "'keyboard'">
                <li class = "collection-item col s12 keyboard_button" v-for = "(button, i) in keyboard"  v-bind:key = "button.id" v-bind:id = "button.id"
                v-bind:class = "keyboard[i].color">
                    <textarea class = " materialize-textarea" 
                        v-bind:class = "{ 'selected' : button.selected }"
                        v-model = "keyboard[i].text"
                        v-on:mousedown.stop = ""
                        v-on:click.ctrl.stop = "selectButton(button.id)"
                        v-on:click.alt.stop = "deleteButton(button.id)"
                        v-on:click.shift.stop = "switchButtonColor(i)"
                        >
                        </textarea>
                <div class = 'message-delete-button'></div>
                </li>
                    
                
            </ul>
            <a class=" waves-effect waves-light btn-small" v-on:click.prevent = "addButton()">  <i class="material-icons">add</i></a>
        </div>
        <div class = "down-btn" v-on:click.ctrl.stop = "selectButton(-1)"></div>
    </div>
</template>
<script>

export default {
    props : {
        text : {
            type : String,
            default(){
                return 'Text'
            },
        },
        id :{
            type : Number,
            default(){
                return 0
            }
        },
        _keyboard : {
            type : Array
            
        },
        position : {
            type : Array,
            default(){
                return [0,0]
            }
        },
        isStart : {
            type : Boolean,
            default(){
                return false
            }
        }
    },
    data(){
        return{
            
            message : this.text,
            keyboard : this._keyboard,
            tmp_click_x : '',
            tmp_click_y : '',
            tmp_node_x : '',
            tmp_node_y : '',
            draggable : false,
            geometry: {
                posX : this.position[0],
                posY : this.position[1],
                height : '',
                width : '',
                keyboard : []
            },
            menu_to_show :'',
            
            btn_colors : [
                'white','blue','green','red'
            ],
            
           
        }
    },
    methods : {
        updatePos($event){
                this.geometry.posX = this.tmp_node_x + $event.pageX - this.tmp_click_x
                
                this.geometry.posY = this.tmp_node_y + $event.pageY - this.tmp_click_y
                this.updateGeometry('posX', this.geometry.posX)
                this.updateGeometry('posY', this.geometry.posY) 
                
        },
        startDrag($event){
            
            this.tmp_click_x = $event.pageX
            this.tmp_click_y = $event.pageY
            this.tmp_node_x = this.geometry.posX
            this.tmp_node_y = this.geometry.posY
            
            this.draggable = true
            window.addEventListener('mousemove', this.updatePos)
            window.addEventListener('mouseup', this.stopDrag)
        },
        stopDrag($event){
           this.draggable = false
           
           window.removeEventListener('mousemove', this.updatePos)
        },
        updateGeometry(field,value){
            const data = {
                field : field,
                value : value
            }
            this.$emit('updategeometry',data)
        },
        updateNode(field,value){
             const data = {
                field : field,
                value : value
            }
            this.$emit('updatenode',data)
            
        },
        addButton(){
            let max = 0
            this.keyboard.forEach(el =>{
                if(el.id > max){
                    max = el.id
                }
            })
            this.keyboard.push( {
                    id : max + 1,
                    text : "message",
                    link : '',
                    color : 'white'
                })
            this.$nextTick( () => {
                this.calcNodeGeometry()
                
                Object.keys(this.geometry).forEach( field =>{
                    this.updateGeometry(field,this.geometry[field])
                })
          })  
        },
        deleteButton(id){
            const index = this.keyboard.findIndex(button => button.id == id)
            this.keyboard.splice(index,1)
            this.$nextTick( () => {
                this.calcNodeGeometry()
                Object.keys(this.geometry).forEach( field =>{
                    this.updateGeometry(field,this.geometry[field])
                })
            })  
        },
        calcNodeGeometry(){
            
            this.geometry.keyboard = []
            this.geometry.height = this.$refs.node.clientHeight
            this.geometry.width = this.$refs.node.clientWidth
            this.$refs.keyboard.childNodes.forEach( btn=>{
                
                this.geometry.keyboard.push(
                    {
                        id : btn.id / 1,
                        offsetX : this.$refs.keyboard.offsetLeft + btn.offsetLeft,
                        offsetY : this.$refs.keyboard.offsetTop + btn.offsetTop,
                        width : btn.clientWidth,
                        height : btn.clientHeight
                    }
                )
            })
            
            
            
        },
        switchButtonColor(index){
            if(this.keyboard[index].color)
            {
                let cur_color_index = this.btn_colors.findIndex(color => color == this.keyboard[index].color) || 0
                let next_color_index = (cur_color_index + 1)  % this.btn_colors.length 
                console.log(cur_color_index,next_color_index)
                this.keyboard[index].color = this.btn_colors[next_color_index]
                console.log(this.keyboard[index].color)
            }
            else{
                this.keyboard[index].color = 'blue'
            }
            let buttons = this.keyboard
            
            this.updateNode('links',this.keyboard)

        },
        selectButton(id){
            
            
            this.$emit('selectbutton', {id})
        },
    
    },
    created(){
        
    },
    mounted(){
        this.calcNodeGeometry()
        Object.keys(this.geometry).forEach( field =>{
            this.updateGeometry(field,this.geometry[field])
        })
        
    },
    watch : {
        message : function(val){
            this.updateNode('message',val)
        }
        
    }
    
}
</script>
<style lang="scss">
    .node{
        position : absolute;
        width : 200px;
        min-height: 150px;
        border : 3px solid #616161;
        &.draggable{
            z-index : 20
        }
        &:hover{
            .entry-marker{
                opacity : 1 
            }
            .delete-button{
                opacity : 1
            }
            
        }
        .entry-marker{
            position: absolute;
            top:0;
            right:3%;
            
            opacity : 0;
            transition: 0.3s;
            width : 20px;
            height : 20px;
            border-radius : 5px;
            z-index : 100;
        }
        .down-btn{
            position:absolute;
            bottom:0;
            left:90px;
            height:10px;
            width:20px;
            background-color:black
        }
        .delete-button{
            position: absolute;
            top:0;
            left:0;
            
            opacity : 0;
            transition: 0.3s;
            width : 20px;
            height : 20px;
            border-radius : 5px;
            z-index : 100;
        }
        .node_keyboard {
            overflow: visible;
            .keyboard_button{
                
                position : relative;
                
                .btn-color {
                    position: absolute;
                    
                    right  : 0;
                    top: 30%;
                    width: 10px;
                    height:10px;
                    border-radius : 5px;
                    background-color : rgb(109, 9, 129);
                    opacity: 0;
                    transition: 0.3s;
                    &:hover{
                        .color-menu{
                            opacity :1;
                            width:100px;
                        }
                    }
                }
                &:hover{
                    .btn-color{
                        opacity : 1;
                        
                    }
                }
            }
        }
        .color-menu{
            width : 0;
            opacity:0;
            position: absolute;
            z-index:100;
            left:100%;
            
            top:0;
        }
    }
</style>




