import Vue from 'vue'
import App from './App.vue'
import config from '../config.json'
import Router from 'vue-router'
import BotPanel from './components/bot-panel.vue'
import StartMenu from './components/start-menu.vue'
import vkBot from './client_side/vkBot.js'
import axios from 'axios'
Vue.prototype.$config = config
Vue.prototype.$createVkBot = vkBot
Vue.use(Router)
const router = new Router({
  routes :[
    {
      path : '/',
      redirect : '/start'
    },
    {
      path : '/bot/:id',
      component : BotPanel,
      props : true
    },
    {
      path : '/local/:id',
      component : BotPanel,
      props : (route) =>({
        id : route.params.id,
        inBrowser: true
      })
    },
    {
      path : '/start',
      component : StartMenu
    }
  ]
})


new Vue({
  el: '#app',
  render: h => h(App),
  router,
  data : {
    botList : [],
    localBots : [],
    server : null,
    token : null,
    connecton : null,
    authData : {}
  },
  methods :{
    async signin(payload){
      try{
        const res = await axios.post(this.server + '/signin', payload)
        
        this.token = res.data.token
        localStorage.setItem('token', JSON.stringify(this.token))
        return res
      }
      catch(err){
        console.log(err)
      }
      
    },
    async signup(payload){
      const res = await axios.post(this.server + '/signup', payload)
      return res.data 
    }

  },
  created(){
    this.server = localStorage.getItem('server') || null
    this.token = localStorage.getItem('token') || null
   
  }
})



