<template>
    <div class = "menu" id = "menu"  v-on:click.right.stop.prevent = "" v-bind:style = "{ left : posX + 'px', top : posY + 'px' }" tabindex="1234">
         <ul class="collection">
            <li class="collection-item" v-on:click.stop.prevent = "$emit('createnode')">create node</li>
            
        </ul>
    </div>
</template>
<script>
export default {
    props : {
        posX :{
            default(){
                return 0
            }
        },
        posY : {
            default(){
                return 0
            }
        }

    }
}
</script>
<style lang="scss">
    .menu{
        width:150px;
        position: absolute;
        .collection{
            position: absolute;
            left:0;
            top:0
        }
    }
</style>


