
import EventEmitter from '../events.js'
import request from '../../jsonp-req'
class Session extends EventEmitter{
    constructor(token,group_id){
        super()
        this.token = token
        this.group_id = group_id
        this.reqCycle = null
        this.lastUpdateDate =  null
        this.status = 0
    }

    connect(){
        return new Promise((resolve, reject) =>{
            request.get('https://api.vk.com/method/groups.getLongPollServer?group_id='+ this.group_id + '&access_token=' + this.token + '&v=5.74')
                .then(data =>{
                    resolve(data)
                })
        })        
        
    }
    polling(){
            this.status = 1
            request.get('https://api.vk.com/method/messages.getConversations?group_id='+ this.group_id + '&filters=unread&access_token=' + this.token + '&v=5.74' )
                .then(res=>{
                    console.log(res)
                    const new_messages = []
                    for(let i in res.data.response.items){
                        const {from_id} = res.data.response.items[i].last_message
                        if(res.data.response.items[i].last_message.date > this.lastUpdateDate && from_id > 0){
                            new_messages.push({type : 'message_new', object : {user_id : from_id,...res.data.response.items[i].last_message}})
                        }
                    }
                   
                    if(new_messages.length != 0)
                        this.emit('update', new_messages)
                    if(!res.data.error){
                        this.reqCycle = setTimeout(this.polling.bind(this), 1000)
                    }
                    else{
                        this.status = 0
                        clearTimeout(this.reqCycle)
                    }  
                    
                }).catch(err=>{
                    this.status = 0
                    clearTimeout(this.reqCycle)
                    console.log(err)
                    
                })    
        }
        stop(){
            this.status = 0
            clearTimeout(this.reqCycle)
        }
    
    
}
export default Session




