
window.CallbackRegistry = {}
window.lastCbId = 0
const req = (url, onSuccess, onErr) =>{
    let scriptOk = false
    const cbName = 'cb' + window.lastCbId
    window.lastCbId += 1
    url += ~url.indexOf('?') ? '&' : '?';
    url += 'callback=CallbackRegistry.' + cbName;
    window.CallbackRegistry[cbName] = function(data) {
        scriptOk = true
        console.log(data)
        delete CallbackRegistry[cbName]
        onSuccess(data)
    }
    function checkCallback() {
        if (scriptOk) return
        delete CallbackRegistry[cbName]
        onErr(url)
    }
    const script = document.createElement('script')
    script.onload = script.onerror = checkCallback;
    script.src = url;
    script.type ="text/javascript"

    document.body.appendChild(script);
}
const promiseReq = (url) => new Promise((resolve, reject) =>{
    
    req(url,(data)=>{
            
            resolve({data})
        },
        (err) =>{
            reject(err)
        }
    )
})
export default {get : promiseReq}


