const isCollide = (box1,box2) => {
    
    if(box1.x1 < box2.x2 && box1.x2 > box2.x1 && box1.y1 > box2.y2 && box1.y2 < box2.y1){
        return true
    }
    return false
}
const getCornerDot ={
    topRight : (box)=> { return {x : box.x2 + 30 + Math.floor(Math.random() * Math.floor(40)) - 20, y : box.y2 - 30 + Math.floor(Math.random() * Math.floor(40)) - 20} },
    topLeft : (box) => { return {x : box.x1 - 30 + Math.floor(Math.random() * Math.floor(40)) - 20, y : box.y2 - 30} },
    bottomRight : (box) => { return {x : box.x2 + 30+ Math.floor(Math.random() * Math.floor(40)) - 20, y : box.y1 + 30+ Math.floor(Math.random() * Math.floor(40)) - 20} },
    bottomLeft : (box) => { return {x : box.x1 - 30+ Math.floor(Math.random() * Math.floor(40)) - 20, y : box.y1 + 30+ Math.floor(Math.random() * Math.floor(40)) - 20} }
}
const getRange = (p1,p2) =>(p2.y - p1.y) * (p2.y - p1.y) + (p2.x - p1.x) * (p2.x - p1.x)
const swap = (items, firstIndex, secondIndex) =>{
    const temp = items[firstIndex];
    items[firstIndex] = items[secondIndex];
    items[secondIndex] = temp;
}
const partition = (items, left, right,p1) =>{
    let pivot   = getRange(items[Math.floor((right + left) / 2)],p1),
        i       = left,
        j       = right;
    while (i <= j) {
        while (getRange(items[i],p1) < pivot) {
            i++;
        }
        while (getRange(items[j],p1) > pivot) {
            j--;
        }
        if (i <= j) {
            swap(items, i, j);
            i++;
            j--;
        }
    }
    return i;
}
const quickSort = (items, left, right,p) =>{
    let index;
    if (items.length > 1) {
        index = partition(items, left, right,p);
        if (left < index - 1) {
            quickSort(items, left, index - 1,p);
        }
        if (index < right) {
            quickSort(items, index, right,p);
        }
    }
    return items;
}
const makeLinePath = (p1,p2, obstacles) =>{
    const dots = [p1]
    quickSort(obstacles,0, obstacles.length - 1,p1)
    
    let index = 0
    
    while(index < obstacles.length){
        let _p1 = dots[dots.length-1]
        const yotx = (x) => (p2.y - _p1.y) / (p2.x - _p1.x) * (x - _p1.x) + _p1.y 
        const xoty = (y) => (p2.x - _p1.x) / (p2.y - _p1.y) * (y - _p1.y) + _p1.x
        
        let collisionDot = null
        let minRange = getRange(p2, _p1)
        let corner = null
       
        
        
        if(yotx(obstacles[index].x1) > obstacles[index].y2 && yotx(obstacles[index].x1) < obstacles[index].y1){
                const dot = {y : yotx(obstacles[index].x1), x : obstacles[index].x1}
                const range = getRange(_p1,dot)
                if(range < minRange){
                    collisionDot = dot
                    minRange = range
                    if((p2.y - _p1.y) / (p2.x - _p1.x)>0){
                        corner = 'bottomLeft'
                    }
                    else {
                        corner = 'topLeft'
                    }
                }
            }
        if(yotx(obstacles[index].x2) > obstacles[index].y2 && yotx(obstacles[index].x2) < obstacles[index].y1){
                const dot = {y : yotx(obstacles[index].x2), x : obstacles[index].x2}
                const range = getRange(_p1,dot)
                if(range < minRange){
                    collisionDot = dot
                    minRange = range
                    if((p2.y - _p1.y) / (p2.x - _p1.x)>0){
                        corner = 'topRight'
                    }
                    else {
                        corner = 'bottomRight'
                    }
                }
            }
        if(xoty(obstacles[index].y1) > obstacles[index].x1 && xoty(obstacles[index].y1) < obstacles[index].x2){
                const dot = {y : obstacles[index].y1, x : xoty(obstacles[index].y1)}
                const range = getRange(_p1,dot)
                if(range < minRange){
                    collisionDot = dot
                    minRange = range
                    if((p2.y - _p1.y) / (p2.x - _p1.x)>0){
                        corner = 'bottomLeft'
                    }
                    else {
                        corner = 'bottomRight'
                    }
                }
            }
        if(xoty(obstacles[index].y2) > obstacles[index].x1 && xoty(obstacles[index].y2) < obstacles[index].x2){
                const dot = {y : obstacles[index].y2, x : xoty(obstacles[index].y2)}
                const range = getRange(_p1,dot)
                if(range < minRange){
                    collisionDot = dot
                    minRange = range
                    if((p2.y - _p1.y) / (p2.x - _p1.x)>0){
                        corner = 'topRight'
                    }
                    else {
                        corner = 'topLeft'
                    }
                }
            }
        if(corner){
            
            dots.push(getCornerDot[corner](obstacles[index]))
        }
        else{
            index++
        }
        
    } 
    
    dots.push(p2)
    return dots
}
export {makeLinePath, isCollide}



