<template>
    <div class="grey lighten-4 start-menu">
        <div class = "row"> <i class=" material-icons large col m1 s12">exit_to_app</i></div>
        <div class = "row" v-if = "showForm">
                <div class = "col offset-s3 offset-m4 s6 m4 card white">
                    <div class="input-field col s12">
                        <input id="last_name" type="text"  v-model = "address">
                        <label for="last_name">Backend address</label>
                    </div>
                    <div class="input-field col s12">
                        <input id="password" type="password" class="validate" v-model = "password">
                        <label for="password">Password</label>
                    </div>
                    <a class="waves-effect orange waves-light col s6 btn signup" v-on:click = "signup({password})">signup</a>
                    <a class="waves-effect red waves-light  col s6 btn signin" v-on:click = "signin({password})">signin</a>
                </div>
        </div>
        <div class = "row">
            <ul class = " bot-list col s12 m4  ">
                        <li class = "black-text col s12">Server side bots:</li>
                        <li class = "red-text col s12" v-if = "botList.length === 0">No data :(</li>
                        <router-link class = "collection-item col grey lighten-4  s12" v-for = "(bot,i ) in botList" v-bind:key = "i" :to = "'/bot/' + bot.id">{{bot}}</router-link>
                        <li class = "black-text col s12">Bots in localStorage:</li>
                        <router-link class = "collection-item col grey-text s12 " v-for = "(bot,i ) in localBotList" v-bind:key = "i" :to = "'/local/' + bot.id" >{{bot}}</router-link>
                        <li class = "red-text col s12" v-if = "localBotList.length === 0">No data :(</li>
                        <router-link class = "collection-item col grey lighten-4 s12 black-text" :to = "'/bot/'  + Date.now()">  
                           Create new bot
                        </router-link> 
            </ul>
        </div>
        
    </div>
</template>
<script>
import axios from 'axios'

export default {
    data(){
        return {
            address : this.$root.$data.server,
            botList : [],
            localBotList : [],
            password : '',
            showForm : false
        }
    },
    methods :{
        getBotList(){
            return new Promise((res,rej)=>{
                const token = this.$root.$data.token
                
                
                axios.get(localStorage.getItem('backend') + '/getbots', {headers : {
                    Authorization : token
                }}).then(response =>{
                    this.botList = response.data
                }).catch(err => {
                    console.log(err)
                })
            })
        },
        signin(data){
            
            this.$root.signin(data).then(res=>{

            }).catch(err => {
                console.log(err)
            })
        },
        signup(data){
            this.$root.$methods.signup(data).catch(err =>{
                console.log(err)
            })
        }
        
    },
    created(){
        
        const bots = JSON.parse(localStorage.getItem('bots'))
        for(let i = 0;i<bots.length; ++i){
            this.localBotList.push({ name : bots[i].name, id : bots[i].id })
        }

        
    },
    watch:{
        address : function(val){
            this.$root.$data.$server = val
            localStorage.setItem('server', val)
            
        }
    },
    
    

}
</script>
<style lang="scss">
    .start-menu{
        width:100vw;
        min-height: 100vh;
        
        input{
            border-bottom-color: black!important
        }
        .bot-list{
            a,li{
                text-align: left;
                font-size: 1.5em;
            }
            li{
                font-size: 2em;
            }
        }
    }
</style>


