<template>
    <div class="row settings-form">
    <form class="col m6 offset-m3 s12 white">
      <div class="row">
        <div class="input-field col s6">
          <input  id="vk_token" type="text" class="validate" v-model = "vktoken">
          <label for="vk_token">Vk Token</label>
        </div>
        <div class="input-field col s6">
          <input id="vk_group_id" type="text" class="validate" v-model = "vkgroup">
          <label for="vk_group_id">Vk Group Id</label>
        </div>
      </div>
      
      <div class="row">
        <div class="input-field col s12">
          <input id="tg_token" type="text" class="validate" v-model = "tgtoken">
          <label for="tg_token">Telegram Token</label>
        </div>
      </div>
      <i class="material-icons" v-on:click = "$emit('update', {vktoken,tgtoken,vkgroup})">done</i>

    </form>
  </div>
</template>
<script>
export default {
  props : {
    params:{
      default(){
        return {}
      }
    }
  },
  data(){
    return{
      vktoken : this.params.vktoken,
      tgtoken : this.params.tgtoken,
      vkgroup : this.params.vkgroup
    }
  }
}
</script>
<style lang="scss">
    .settings-form{
        position : fixed;
        width: 100vw;
        top:0;
        z-index:10
    }
</style>


