<template>
    <div class = "bot-selection-list">
        <div class = "item black" v-for = "(bot, i) in botList" v-bind:key = "i" >{{bot.script.name}}
            <div class = "stop-btn"></div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            botList : this.$root.$data.botList
        }
    }
}
</script>
<style lang="scss">
    .local-panel{
        position:fixed;
        bottom:0;
        left:0;
        display : flex;
        flex-direction : row;
        justify-content: flex-start;
        .item{
            padding : 0.5em;
        }
    }
</style>


