const colors = {
    blue : 'primary',
    white : 'secondary',
    green : 'positive',
    red : 'negative'
}
const primary_phrases = {
    unexpected_state : 'UNEXPECTED_STATE',
    incorrect_answer : 'incorrect_answer'
}
const makeKeyboard = (buttons_list) => buttons_list.reduce((acc, el) =>{
    acc.buttons.push([
        { 
            action: { 
                type: "text", 
                payload : "{\"button\": \"1\"}", 
                label: el.text 
            }, 
            color: colors[el.color] || "secondary"
        }, 
    ])
    return acc
},{
    one_time : false,
    buttons : []
})
module.exports = {
    messageProcess(methods,script){
            return function(update,state){
                    
                    let text = update.object.text
                    if(state != undefined)
                        {
                        const current_node = script.nodes.find(node => node.id == state)
                        if(!current_node) return messageProcess(undefined)
                        
                        let selected_link = current_node.links.find(link => link.text == text )
                        
                        let next_node_id = selected_link ? selected_link.target_id : current_node.default_link_target_id
                        if(next_node_id === undefined){
                            next_node_id = current_node.default_link_target_id
                        }
                            
                        if(next_node_id)
                        {
                            const next_node = script.nodes.find(node => node.id == next_node_id)
                            
                            const keyboard = makeKeyboard(next_node.links)
                            methods.message_send({
                                user_id : update.object.user_id,
                                message : next_node.message,
                                keyboard : JSON.stringify(keyboard)
                            })
                            return next_node_id
                        }
                        else 
                        {    
                            methods.message_send({
                                user_id : update.object.user_id,
                                message : current_node.err_message || primary_phrases.incorrect_answer ,
                            })
                            return current_node.id
                        }
                    }
                    else {
                        const start_node = script.nodes.find(node => node.id == script.entry_node) || script.nodes[0]
                        const keyboard = makeKeyboard(start_node.links)
                        methods.message_send({
                            user_id : update.object.user_id,
                            message : start_node.message,
                            keyboard : JSON.stringify(keyboard)
                        })
                        return start_node.id
                    }
            }
    }
}


