<template>
  <div  class = "bot-panel grey lighten-4" v-on:click.right.self.prevent = "togglePanelMenu($event)"  v-bind:style = "{width : app_width + 'px', height: app_height + 'px'}">
    <svg v-bind:style = "{width : app_width + 'px', height: app_height + 'px'}">
      <polyline v-for = "(line,i) in lines" v-bind:key = i stroke="black" stroke-width="3px" fill="none"
        v-bind:points = "line.path_" />
     
  </svg>
  <controlPanel v-on:run = "runBot()" 
                v-on:runlocal = "runInBrowser()"
                 v-on:savelocal = "saveInBrowser($event)"
                 v-on:upload = "save($event)"
                 v-on:togglesettings = "showSettings = !showSettings"
                v-bind:name = "name"    
                v-bind:isLocalyRunning = "isLocalyRunning"></controlPanel>
  <settingsForm v-if = "showSettings" v-on:update = "showSettings = !showSettings; updateParams($event)"
                v-bind:params = "params">

  </settingsForm>
  <node v-for = "node in nodes" 
              v-bind:text = "node.message" 
            v-bind:key = "node.id" 
              v-bind:_keyboard = "node.links" v-bind:id = "node.id"
              v-bind:isStart = "start_node == node.id" 
            v-bind:position = "geometry.find(el => el.id == node.id) ? 
                            [geometry.find(el => el.id == node.id).posX, geometry.find(el => el.id == node.id).posY] :
                              [0,0]" 
            v-on:updategeometry = "updateNodeGeometry(node.id, $event.field,$event.value)" 
            v-on:updatenode = "updateNode(node.id, $event.field,$event.value)"
            v-on:selectbutton = "selected_button = { node_id : node.id, button_id : $event.id }"
            v-on:selectnode= "connect(node.id)"
            v-on:deletenode = "deleteNode(node.id)"
            v-on:markasstart = "start_node = node.id"></node>
      <panelMenu v-show = "showPanelMenu" v-bind:posX = "panelMenuX" 
      :posY = "panelMenuY" v-on:blur = "togglePanelMenu()" v-on:createnode = "createNode({x : panelMenuX, y : panelMenuY}); togglePanelMenu()"></panelMenu>
  </div>
</template>
<script>
import axios from 'axios'
import node from './node.vue'
import panelMenu from './panel-menu'
import settingsForm from './settings-form.vue'
import controls from '../controls.js'
import controlPanel from './control-panel'
import {makeLinePath, isCollide} from '../geometry.js'
const {save, get, start,stop, getStatus, runLocal, stopLocal} = controls(axios, localStorage.getItem('token'),
                                                                          localStorage.getItem('server'))
export default {
  components : {
     node,
     panelMenu,
     controlPanel,
     settingsForm
  },
  props :{
    
    id:{
      default(){
        return null
      }
    },
    inBrowser : {
      default(){
        return false
      }
    }
  },
  data () {
    return {
      meta: '',
      params : {},
      
      isRunning : false,
      isLocalyRunning : false,
      name : null,
      lines : [],
      showSettings:false,
      nodes : [],
      geometry : [],
      width : 2000,
      height : 2000,
      selected_button : '',
      nodes_data : [],
      start_node : '',
      app_height:10000,
      app_width : 10000,
      bot_status : '',
      checkbox_state : false,
      showPanelMenu : false,
      panelMenuX: 0,
      panelMenuY : 0
    }
  },
  methods : {
    get,
    stop,
    start,
    stop,
    save(data){
      this.name = data.new_name
      const {name,nodes,geometry, meta, start_node,params} = this
      save(name, {nodes, geometry,meta, start_node,params})
    },
    getStatus,
    runLocal,
    stopLocal,
    updateParams(data){
      this.params = data;
    },
    togglePanelMenu($event){
      
      this.showPanelMenu = !this.showPanelMenu
      if(this.showPanelMenu){
        this.panelMenuX = $event.pageX
        this.panelMenuY = $event.pageY
        this.$nextTick(()=>{
          document.getElementById('menu').focus()
          document.getElementById('menu').addEventListener('focusout',() => { this.showPanelMenu = false })
        })
      }
    },
    makeLine(source_geometry,button_id,target_geometry){
      const x2 = target_geometry.posX + 100
      const y2 = target_geometry.posY
      let x1
      let y1
      
      if(button_id != -1){
        const source_button = source_geometry.keyboard.find(el => el.id == button_id)
        x1 = x2 > source_button.offsetX + source_geometry.posX + source_button.width / 2 ?  
                  source_geometry.posX + source_button.offsetX + source_button.width  + 20:  
                  source_geometry.posX + source_button.offsetX - 20

        y1 = source_button.offsetY + source_geometry.posY  + source_button.height / 2

       
      }
      else{
        x1 = source_geometry.posX + 100
        y1 = source_geometry.posY + source_geometry.height
        
      }
      
      const obst = []
      let [_x1,_x2] = x1 > x2 ? [x2,x1] : [x1,x2]
      let [_y1,_y2] = y1 > y2 ? [y1,y2] : [y2,y1] 
      
      this.geometry.forEach(el=>{
        
        if(isCollide({ x1 : el.posX, x2 : el.posX + el.width, y1 : el.posY + el.height, y2 : el.posY },
                    {x1 :_x1, y1 : _y1, x2 : _x2, y2 : _y2}))
                    {
                      
                      obst.push({x1 : el.posX, x2 : el.posX + el.width, y1 : el.posY + el.height,y2 : el.posY,
                                 y : el.posY + el.height/2, x: el.posX + el.width/2,width : el.width, height : el.height })
                    }
      })

      let str = []
      makeLinePath({x : x1, y :y1+10}, {y : y2, x : x2},obst).forEach(dot=>{
        str.push(dot.x + ',' + dot.y)
      })
      str = str.join(' ')
      
      
      return { 
              source_node : source_geometry.id,
              source_button : button_id,
              target_node : target_geometry.id, 
              
              path_ : str
            }
      
    },
    updateNodeGeometry(node_id,field,value){
      const node = this.geometry.find(el => el.id === node_id)
      
      if(!node){
        const new_obj = {id : node_id}
        new_obj[field] = value
        this.geometry.push(new_obj)
      } 
      else {
        node[field] = value
      }
      
      
      this.lines = this.lines.filter(line => !(line.source_node == node_id || line.target_node == node_id))
      
    
      this.nodes.forEach(node =>{
        if(node.id != node_id){
          node.links.forEach(link =>{
            if(link.target_id == node_id){
              const source_geometry = this.geometry.find(el => el.id == node.id)
              const target_geometry = this.geometry.find(el => el.id == node_id)
              this.lines.push(this.makeLine(source_geometry, link.id, target_geometry))
            }
            
          })
          if(node.default_link_target_id == node_id){
              const source_geometry = this.geometry.find(el => el.id == node.id)
              const target_geometry = this.geometry.find(el => el.id == node_id)
              this.lines.push(this.makeLine(source_geometry, -1 , target_geometry))
          }
        }
        else{
          const source_geometry = this.geometry.find(el => el.id == node_id)
          node.links.forEach(link =>{
            if(link.target_id){
              const target_geometry = this.geometry.find(el =>el.id == link.target_id)
              this.lines.push(this.makeLine(source_geometry, link.id, target_geometry))
            }
            
          })
          if(node.default_link_target_id){
              const target_geometry = this.geometry.find(el =>el.id == node.default_link_target_id)
              this.lines.push(this.makeLine(source_geometry, -1, target_geometry))
          }
        }

      })

      
     
     
    },
  
    updateNode(node_id,field,value)
    {
        let id = this.nodes.findIndex((el,index) => el.id == node_id)
        this.nodes[id][field] = value
    },
    createNode(pos){
      let max_id = 0
      this.nodes.forEach(node =>{
        const id = node.id / 1
        if(id > max_id) max_id = id
      })
      this.nodes.push({
        id : max_id + 1,
        message : 'message text',
        links : []
      })
      this.geometry.push({
        id : max_id + 1,
        posX : pos.x,
        posY : pos.y,
        keyboard : []
      })
      
    },
    deleteNode(id){
      const node_index = this.nodes.findIndex(node => node.id == id)
      
      let geom_index = this.geometry.findIndex( el => el.id == id)
      
      this.nodes.forEach(node =>{
        node.links.forEach(link=>{
          if(link.target_id == id) {
              link.target_id = null
            }
        })
        if(node.default_link_target_id == id){
          node.default_link_target_id = null
        }
      })
        const arr = []
        this.lines.forEach((line,i) => {
          if(line.target_node != id && line.source_node != id) {
            
            
            arr.push(line)
          
          } 
        
      })
      this.lines = arr
      this.nodes.splice(node_index,1)
      this.geometry.splice(geom_index,1)
      
    },
    connect(node_id){

      if(this.selected_button){
        
        let source_node_index = this.nodes.findIndex(node => node.id == this.selected_button.node_id)
        if(this.selected_button.button_id == -1){
          this.nodes[source_node_index].default_link_target_id = node_id
        }
        else{
          const source_button_index = this.nodes[source_node_index].links.findIndex(link => link.id == this.selected_button.button_id)
          this.nodes[source_node_index].links[source_button_index].target_id = node_id
        }
        
        const source_geometry = this.geometry.find(node => node.id == this.selected_button.node_id)
        const target_geometry = this.geometry.find(node => node.id == node_id)
        const geometry_button_index = source_geometry.keyboard.findIndex(button => button.id == this.selected_button.button_id)
        
        const line_index = this.lines.findIndex(line => {
            
            return line.source_node == this.selected_button.node_id && line.source_button == this.selected_button.button_id
          })
        
        if( line_index != -1) this.lines.splice(line_index,1)
        this.lines.push(this.makeLine(source_geometry,this.selected_button.button_id, target_geometry))
        this.selected_button = null
      }
    },
    
    
    runBot(){
      
      this.start(this.name_).then(res=>{
        if(res.data.status) this.bot_status = 1
        
      }).catch(err=> {console.log(err);  this.bot_status = 0})
      
      
    },
    stopBot(){
      this.stop(this.name_).then(res=>{
        if(res.data.status ) this.bot_status = 0
        
      }).catch(err => console.log(err))
      
      
    },
    getBotStatus(){
      this.getStatus(this.name_).then(res =>{
        this.bot_status = res.data.status || 0
      })
    
      
    },
    runInBrowser(){
      const {nodes,start_node, params,id,name} = this
      const script = {nodes,start_node, params}
     
      
      
      const res = this.runLocal(script)
      if(res){
        
        
        
        this.isLocalyRunning = true
      }
      
        else {
          this.stopLocal(script)
          this.isLocalyRunning = false
          }
      
      
    },
    saveInBrowser(data){
      const str = localStorage.getItem('bots') || "[]"
      const bots = JSON.parse(str)
      this.name = data.new_name
      const index = bots.findIndex(el => el.id == this.id)
     
      const {name,id,nodes,geometry, params,start_node} = this
      if(index >= 0){
        bots[index] = {name,id,nodes,geometry, params,start_node}
      }
      else{
        bots.push({name,id,nodes,geometry, params,start_node})
      }
      localStorage.setItem('bots', JSON.stringify(bots))
    }
    
    
  },
  created() {

    const bot = this.$root.$data.localBots.find(item => item.id === this.id)
    if(bot){
      if(bot.updateRecivier.status === 1){
        this.isLocalyRunning = true
      }
    }
    if(this.inBrowser){
      const bots = JSON.parse(localStorage.getItem('bots'))
      for(let i = 0; i< bots.length; ++i){
        if(bots[i].id == this.id){
          this.params = bots[i].params
          this.nodes = bots[i].nodes
          this.geometry = bots[i].geometry
          this.start_node = bots[i].start_node
          this.meta = bots[i].meta
          this.name = bots[i].name
        }
      }
      
      return
    }
    this.get(this.name_).then(res => 
    {
      this.nodes = res.data.nodes || []
      this.geometry = res.data.geometry || []
      this.start_node = res.data.start_node || ''
      this.meta = res.data.meta
      this.params = res.data.params || {}
      if(!this.geometry)
      {
        this.geometry = []
      }
      if(!this.nodes){
        this.nodes = []
      }
      this.nodes.forEach(el=>
      {
        if(el.links){
          el.links.forEach(link=>{
            if(!link.color) link.color = 'white'
          })
        }
      })
      this.geometry.forEach(( node, i ) =>{
        let index = this.nodes.findIndex(el => el.id == node.id)
        if( index == -1) {
          this.geometry.splice(i,1)
        }
      })
      this.lines = []
      this.nodes.forEach(node=>{
        const source_geometry = this.geometry.find( el => el.id == node.id)
        node.links.forEach((link) => {
          const target_geometry = this.geometry.find(el => el.id == link.target_id)
          if(!target_geometry){
            link.target_id = null
            return
          }
          this.lines.push(this.makeLine(source_geometry,link.id,target_geometry))

        })
        if(node.default_link_target_id){
          const target_geometry  = this.geometry.find(el => el.id == node.default_link_target_id)
          if(!target_geometry){
            node.default_link_target_id = null
          }
          else{
            this.lines.push(this.makeLine(source_geometry,-1,target_geometry))
          }
        }
      })
      this.getBotStatus()
    }).catch(err =>{
      console.log(err)
      this.nodes =  []
      this.geometry =  []
      this.start_node = ''
    })

  },
  
  
  

  
   
}
</script>

<style lang="scss">
.bot-panel {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  
  .run-btn{
    position : fixed;
    right: 0;
    top:0;
  }
  .stop-btn{
    position : fixed;
    right: 0;
    bottom:0;
  }
}
svg{
  position : absolute;
  left:0;
  top:0;
  width:100%;
  height:100%;
  z-index : 100;
  pointer-events: none
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
.control-panel{
  position: fixed;
  width: 100vw;
  bottom: 0;
  z-index : 100;
  .row{
    margin-bottom : 0;
  }
  .save-btn{
    width : 100%;
    margin-top: 1em;

  }
  .bot-toogle{
    
    margin:0.5em;
    display : flex;
    flex-direction: column;
    justify-content: flex-end
  }
  .state-pic{
    height:3em;
    width:100%;
    background-size : contain;
    background-position: center;
    background-repeat: no-repeat;
    background-image : url('../assets/robot_red.png');
    &.active{
      background-image : url('../assets/robot_green.png');
    }
    
  }
  .control-panel{
    
  }
  
}
.node.card.container{
  margin:0!important
}
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar:horizontal {
  position: fixed;
  top:0!important;
  height: 5px;
}
/* Track */
::-webkit-scrollbar-track {
  background: rgba(50, 1, 56, 0.377);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(26, 1, 37);
}

/* Handle on hover */
::-webkit-scrollbar-corner, ::-webkit-resizer {
  background : rgba(70, 16, 131, 0.342)
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
</style>




