
import Bot from './bot.js'
import BotStateWorker from './botStateWorker.js'

import Session from './vk_api/session.js'

import vkApiMethods from './vk_api/methods.js'
const {messageProcess} = require('./bot-logic/messageProcessing.js')
console.log(messageProcess)


const stateWorker = new BotStateWorker()


export default (script) =>{
    const { vkgroup, vktoken } = script.params
    const methods = vkApiMethods({group_id : vkgroup, token : vktoken}, '5.74')
    const messageWorker = messageProcess(methods, script)
    const updateFunc = function(updates ){
        
        updates.forEach(update =>{
            
            if( update.type === 'message_new' ){
                const state = this.stateManager.getState(update.object.user_id)
                const next_state = messageWorker(update, state)
                this.stateManager.setState(update.object.user_id,next_state)
                console.log('vk_bot' + ' set state of user' + update.object.user_id + ' to ' + next_state  + 
                    '\n --------------')
            }
        })
    }
    const session = new Session(vktoken, vkgroup)
    return new Bot(updateFunc, stateWorker, script, session)
}




