export default (axios, token, baseURL) =>{
    const instance = axios.create({
        baseURL
      });
      
      
      instance.defaults.headers.common['Authorization'] = token;
    return {
        start(botId){
            return instance.post('/start',{botId})
        },
        stop(botId){
            return instance.post('/stop',{botId})
        },
        save(botId,script){
            return instance.post('/save',{botId, script})
        },
        get(botId){
            return instance.get('/get/' + botId)
        },
        getStatus(botId){
            return instance.get('/status/' + botId)
        },
        save(botId, script){
            return instance.post('/save', {botId, script})
        },
        runLocal(script){
            
            let bot = this.$root.$data.localBots.find(bot => bot.script.name === script.name)
            if(!bot){
                bot = this.$createVkBot(script)
                bot.updateReciever.connect().then(res =>{
                    bot.updateReciever.polling(res.data.response)
                  })
                this.$root.$data.localBots.push(bot)
                return true
            }
            else{
                if(bot.updateReciever.status === 0){
                    bot.updateReciever.connect().then(res =>{
                        bot.updateReciever.polling(res.data.response)
                      })
                      return true
                }
                return false
            }
        },
        stopLocal(script){
            let bot = this.$root.$data.localBots.find(bot => bot.script.name === script.name)
            
            if(!bot){
                return 
            }
            else{
                bot.updateReciever.stop()
                
            }
        }
    }   
}


