<template>
    <div class = "control-panel">
        <textarea v-model = "new_name"></textarea>
        <div  data-attr = "run" v-on:click = "$emit('run')" v-if = "$root.$data.connection">
            <i class="material-icons" v-if = "running">stop</i>
            <i class="material-icons" v-if = "!running">play_arrow</i>
        </div>
        <div v-else><i class="material-icons grey-text">play_arrow</i></div>
        <div  data-attr ="upload to the server" v-on:click = "$emit('upload', {new_name})" v-if = "$root.$data.connection">
            <i class="material-icons">cloud_upload</i>
        </div>
        <div v-else><i class="material-icons grey-text">cloud_upload</i></div>
        <div data-attr = "run in browser" v-on:click = "$emit('runlocal')">
            <i class="material-icons" v-if = "isLocalyRunning">stop</i>
            <i class="material-icons" v-else>play_arrow</i>
        </div>
        <div data-attr = "save in local storage" v-on:click = "$emit('savelocal',{new_name})"><i class="material-icons" >save</i></div>
        <div><i class="material-icons" v-on:click = "$emit('togglesettings')" >settings</i></div>
        
    </div>
</template>
<script>
export default {
    props :{
        running : {
            default(){
                return false
            }
        },
        isLocalyRunning : {
            default(){
                return false
            }
        },
        name : {
            default(){
                return ''
            }
        },

    },
    data(){
        return{
            new_name : this.name
        }
    }
}
</script>
<style lang="scss">
    .control-panel{
        width:5vw;
        position:fixed;
        top:0;
        left: 0;
        display:flex;
        flex-direction: column;
        justify-content: flex-start;
   
    }
</style>


