class Bot {
    constructor(updateFunc, stateManager, script, updateReciever){
        this.updateFunc = updateFunc
        this.stateManager = stateManager
        this.script = script
        this.updateReciever = updateReciever
        updateReciever.on('update', updates =>{ this.updateFunc( updates) })
    }
}
module.exports = Bot


